<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="6" sm="8">
          <b-card no-body class="mx-4">
            <b-card-body class="p-4">
              <h1>Complete Registration</h1>
              <p class="text-muted">Complete RangePro Registration.</p>

              <span class="form-group__message text-danger" v-if="!$v.oldPassword.required">Temporary Password Required.</span>
              <b-input-group class="mb-3" v-bind:class="{ 'form-group--error': $v.oldPassword.$error }">
                <div class="input-group-prepend">
                  <span class="input-group-text"><i class="icon-lock"></i></span>
                </div>
                <input type="password" class="form-control" placeholder="Temporary Password" v-model="oldPassword">
              </b-input-group>

              <b-input-group class="mb-3">
                <div class="input-group-prepend">
                  <span class="input-group-text"><i class="icon-lock"></i></span>
                </div>
                <input type="password" class="form-control" placeholder="New Password" v-model="newPassword">
              </b-input-group>

              <span class="form-group__message text-danger" v-if="!$v.newRepeat.sameAsPassword">Passwords Must Be Identical.</span>
              <b-input-group class="mb-3" v-bind:class="{ 'form-group--error': $v.newRepeat.$error }">
                <div class="input-group-prepend">
                  <span class="input-group-text"><i class="icon-lock"></i></span>
                </div>
                <input type="password" class="form-control" placeholder="Current Password" v-model="newRepeat">
              </b-input-group>

              <b-row>
                <b-col cols="2">

                </b-col>
                <b-col cols="10">
                  <span class="text-danger">{{ this.$route.params.error }}</span>
                </b-col>
              </b-row>

              <b-button @click="change(username, oldPassword, newRepeat)" variant="success" block :disabled="$v.$invalid">Change Password</b-button>
            </b-card-body>
          </b-card>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
  import auth from '../services/auth'
  import { required, sameAs } from 'vuelidate/lib/validators'

  export default {
    name: 'LoginChangePassword',
    data () {
      return {
        oldPassword: '',
        newPassword: '',
        newRepeat: '',
        username: ''
      }
    },
    mounted () {
      this.username = this.$route.params.username
    },
    methods: {
      change (user, oldPassword, newPassword) {
        return auth.adminChange(user, oldPassword, newPassword)
      }
    },
    validations: {
      oldPassword: {
        required
      },
      newRepeat: {
        sameAsPassword: sameAs('newPassword')
      }
    }
  }
</script>



