<template>
  <div class="col-12">

    <b-card no-body class="bg-white" bg-variant="dark" text-variant="dark">
      <b-tabs pills card>
        <b-tab title="Active Members" active>

          <b-row>
            <b-col md="6" class="my-1">
              <b-form-group horizontal label="Search" class="mb-3 h4">
                <b-input-group>
                  <b-form-input v-model="active_filter" placeholder="Type to Search" />
                  <b-input-group-append>
                    <b-btn :disabled="!active_filter" @click="active_filter = ''">Clear</b-btn>
                  </b-input-group-append>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-row>

          <b-table striped hover :filter="active_filter" :items="active_members" :fields="active_member_fields">

            <template slot="first_name" slot-scope="data">
              <router-link :to="{ name: 'Member Detail', params: { id: data.item.id }}">{{ data.item.first_name}}</router-link>
            </template>

            <template slot="last_name" slot-scope="data">
              <router-link :to="{ name: 'Member Detail', params: { id: data.item.id }}">{{ data.item.last_name}}</router-link>
            </template>

            <template slot="status" slot-scope="data">
              {{ data.item.status.toUpperCase() }}
            </template>

            <template slot="show_details" slot-scope="row">
              <!-- we use @click.stop here to prevent emitting of a 'row-clicked' event  -->
              <b-button size="sm" @click.stop="row.toggleDetails" class="mr-2">
                {{ row.detailsShowing ? 'Hide' : 'Show'}} Details
              </b-button>
            </template>
            <template slot="row-details" slot-scope="row">
              <b-card>
                <b-row class="mb-2" v-for="item in row.item.additional_members">
                  <b-col sm="3" class="text-sm-right"><b>Member Name:</b></b-col>
                  <b-col>{{ item.first_name }} {{ item.last_name }}</b-col>
                </b-row>
              </b-card>
            </template>
          </b-table>

          <nav>
            <b-pagination :total-rows="getRowCount(active_members)" :per-page="active_perPage" v-model="active_currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
          </nav>

        </b-tab>
        <b-tab title="All Registered Users">

          <b-row>
            <b-col md="6" class="my-1">
              <b-form-group horizontal label="Search" class="mb-3 h4">
                <b-input-group>
                  <b-form-input v-model="all_filter" placeholder="Type to Search" />
                  <b-input-group-append>
                    <b-btn :disabled="!all_filter" @click="all_filter = ''">Clear</b-btn>
                  </b-input-group-append>
                </b-input-group>
              </b-form-group>
            </b-col>
          </b-row>

          <b-table striped hover :filter="all_filter" :items="all_members" :fields="all_member_fields">

            <template slot="first_name" slot-scope="data">
              <router-link :to="{ name: 'Member Detail', params: { id: data.item.id }}">{{ data.item.first_name}}</router-link>
            </template>

            <template slot="last_name" slot-scope="data">
              <router-link :to="{ name: 'Member Detail', params: { id: data.item.id }}">{{ data.item.last_name}}</router-link>
            </template>

          </b-table>

          <nav>
            <b-pagination :total-rows="getRowCount(all_members)" :per-page="all_perPage" v-model="all_currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
          </nav>

        </b-tab>
      </b-tabs>
    </b-card>

  </div>
</template>

<script>
  import rangepro from '@/services/rangepro'

  export default {
    name: 'Members',
    data () {
      return {
        loading: false,
        active_members: [],
        active_filter: null,
        all_members: [],
        all_filter: null,
        active_member_fields: [
          {
            key: 'first_name',
            label: 'First Name',
            sortable: true
          },
          {
            key: 'last_name',
            label: 'Last Name',
            sortable: true
          },
          {
            key: 'status',
            label: 'Status'
          },
          {
            key: 'plan_name',
            label: 'Plan'
          },
          {
            key: 'email',
            label: 'Email'
          },
          {
            key: 'phone',
            label: 'Phone'
          },
          {
            key: 'show_details',
            label: 'Authorized Members'
          }
        ],
        all_member_fields: [
          {
            key: 'first_name',
            label: 'First Name',
            sortable: true
          },
          {
            key: 'last_name',
            label: 'Last Name',
            sortable: true
          },
          {
            key: 'email',
            label: 'Email'
          },
          {
            key: 'phone',
            label: 'Phone'
          }
        ],
        active_currentPage: 1,
        active_perPage: 25,
        active_totalRows: 0,
        all_currentPage: 1,
        all_perPage: 25,
        all_totalRows: 0
      }
    },
    mounted: function () {
      this.getActiveMembers()
      this.getAllMembers()
    },
    methods: {
      getActiveMembers () {
        this.loading = true
        rangepro.get_secure('store/members/active', this.handleActiveMemberResponse, 'GetStoreActiveMembers')
      },
      getAllMembers () {
        this.loading = true
        rangepro.get_secure('store/members/all', this.handleAllMemberResponse, 'GetStoreAllMembers')
      },
      handleActiveMemberResponse (data, success) {
        if (success) {
          this.active_members = data.members
          this.loading = false
        } else {
          this.loading = false
        }
      },
      handleAllMemberResponse (data, success) {
        if (success) {
          this.all_members = data.members
          this.loading = false
        } else {
          this.loading = false
        }
      },
      getRowCount (items) {
        return items.length
      }
    }
  }
</script>

<style scoped>

</style>



