<template>
  <aside class="aside-menu text-center">
    <b-card no-body class="bg-dark mb-1" bg-variant="dark" text-variant="light">
      <p class="m-1">LAST 10 WAIVERS</p>
      <b-button :class="{'btn-success': (loading)}" type="submit" variant="primary" size="sm" @click="getSignatures()" :disabled="loading"><i class="fa fa-refresh"></i> REFRESH</b-button>
    </b-card>
    <b-card no-body class="m-1" v-for="item in signatures.slice(0, 10)">
      <h6 class="m-1">{{ item.first_name }} {{ item.last_name }}</h6>
      <p class="text-primary m-1">{{ item.waiver_name.toUpperCase() }}</p>
      <b-button class="m-1" type="submit" size="sm" variant="warning" @click="openWaiver(data.item.id)"><i class="fa fa-file"></i> VIEW</b-button>
    </b-card>
  </aside>
</template>

<script>
  import rangepro from '@/services/rangepro'
  import formats from '@/services/formats'

  export default {
    name: 'c-aside',
    data () {
      return {
        loading: false,
        signature_date: formats.get_date(),
        signatures: []
      }
    },
    methods: {
      getSignatures (response) {
        this.loading = true
        this.signatures = []
        rangepro.get_secure('store/waiver/signatures/' + this.signature_date, this.handleSignatureResponse, 'GetWaiverSignatures')
      },
      handleSignatureResponse (data, success) {
        if (success) {
          this.signatures = data.signatures
          this.loading = false
        } else {
          this.loading = false
        }
      }
    }
  }
</script>



